*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%; /* Prevent font scaling in landscape on iOS */
  touch-action: manipulation; /* Improve touch responsiveness */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* Mobile nav */
@media screen and (max-width: 768px) {
  .nav-links { position: absolute; top: 60px; left: -100%; flex-direction: column; width: 100%; background: white; transition: left 0.3s ease; }
  .nav-links.open { left: 0; }
  .nav-links ul { flex-direction: column; gap: 16px; padding: 16px; }
  .nav-links a { padding: 12px 0; min-height: 44px; display: block; font-size: 18px; }
  .navbar-main { padding: 12px; }
  .logo { font-size: 24px; }
  .search-bar input { width: 150px; }
  .account-btn, .city-selector, .hamburger { min-width: 44px; min-height: 44px; }
  .account-dropdown { width: 280px; }
}

/* Small mobile phones */
@media screen and (max-width: 480px) {
  .navbar-main { padding: 8px; }
  .logo { font-size: 20px; }
  .nav-links ul { gap: 12px; padding: 12px; }
  .nav-links a { padding: 10px 0; font-size: 18px; }
  .account-btn { margin-left: 5px; font-size: 16px; }
  .hamburger div { width: 20px; height: 2px; margin: 3px 0; }
  .account-dropdown { width: 260px; }
}